import React, { Component } from 'react';
import { Route } from 'react-router-dom';
import Home from './home';
import Licenses from './licenses';
import Releases from './releases';
import Settings from './settings';
import './index.scss';

export default class Dashboard extends Component {
    render() {
        return (<div className="dashboard-page">
            <div className="header-content">
                <div className="header-title">
                    <img src={require('../../assets/images/logo.png').default} alt="logo" />
                    <div className="title">Dashboard</div>
                </div>
                <div className="user-info">
                    <div className="name">Alexhehe94#0001</div>
                    <div className="avatar">
                        <img src={require('../../assets/images/logo.png').default} alt="avatar" />
                    </div>
                </div>
            </div>
            <div className="page-tab-content">
                <div className="tab-content">
                    <div className="tab-item active">Home</div>
                    <div className="tab-item">License</div>
                    <div className="tab-item">Releases</div>
                    <div className="tab-item">Settings</div>
                </div>
            </div>
            <Route path="/dashboard/home" component={Home} />
            <Route path="/dashboard/licenses" component={Licenses} />
            <Route path="/dashboard/releases" component={Releases} />
            <Route path="/dashboard/settings" component={Settings} />
        </div>);
    }
}